import React, { useEffect, useState } from "react"
import { Text, Flex, IconButton } from "@chakra-ui/react"
import moment from "moment"
import 'moment/locale/zh-tw'
import { SettingsIcon, TimeIcon } from '@chakra-ui/icons'
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverHeader,
  PopoverBody,
  PopoverArrow,
  PopoverCloseButton,
} from '@chakra-ui/react'
import "../css/index.css"
import alarmSound from "../sound/alarm.wav";

const secs = 1800;

const IndexPage = () => {
  const [time, setTime] = useState(moment().format('HH:mm:ss'));
  const [textColor, setTextColor] = useState("#000000");
  const [bgColor, setBgColor] = useState("#FFFFFF");
  const [date, setDate] = useState(moment().format("YYYY/MM/DD dddd"));
  const [counter, setCounter] = useState(secs);
  const [timer, setTimer] = useState(false);

  
    const playSound = () => {
      const audioElement = new Audio(alarmSound);
      audioElement.play();
    };
  
  

  useEffect(() => {
    const interval = setInterval(
      () => {
        setTime(moment().format('HH:mm:ss'))
        setDate(moment().format("YYYY/MM/DD dddd"))
        if (timer){
          if (counter === 0) {
            playSound();
            setCounter(secs);
            setTimer(false);
          } else {
            setCounter(counter - 1);
          }
        }
      },
      1000
    );

    return () => {
      clearInterval(interval);
    }
  }, [counter, timer]);

  return (
    <Flex w="100%" h='100%' bgColor={bgColor} align='center' justify='center' direction="column">
      <Flex><Text fontSize="72px" color={textColor}>{date}</Text></Flex>
      <Flex><Text fontSize="240px" color={textColor}>{time}&nbsp;</Text></Flex>
      <Flex>{parseInt(counter/60) + ":" + counter % 60}</Flex>
      <Popover>
        <PopoverTrigger>
          <IconButton colorScheme='none' icon={<SettingsIcon color={textColor} />} />
        </PopoverTrigger>
        <PopoverContent>
          <PopoverArrow />
          <PopoverCloseButton />
          <PopoverHeader>Settings</PopoverHeader>
          <PopoverBody>
            <Flex>
              <label>Text Color:
                <input type='color' value={textColor} onChange={e => setTextColor(e.target.value)} placeholder='Text Color'></input></label>
              <label>Background Color:
                <input type='color' value={bgColor} onChange={e => setBgColor(e.target.value)}></input></label>
            </Flex>
          </PopoverBody>
        </PopoverContent>
      </Popover>
      <IconButton colorScheme='none' icon={<TimeIcon color={textColor} onClick={() => setTimer(true)}/>} />
    </Flex>
  )
}

export default IndexPage
